<template>
  <el-form :inline="true" :model="formInline" class="demo-form-inline">
    <el-form-item label="">
      <el-input v-model="formInline.user" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formInline.option" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.region" placeholder="">
        <el-option label="" value="shanghai"></el-option>
        <el-option label="" value="beijing"></el-option>
      </el-select>
    </el-form-item><el-form-item>
    <el-button type="success" @click="onSubmit"></el-button>
  </el-form-item>
  </el-form>
</template>

<script>
    export default {
        name: "inlineForm",
      data(){
        return {
          formInline: {
            user: '',
            region: '',
            option:''
          }
        }
      }
    }
</script>

<style scoped>

</style>



