<template>
  <div>
    <v-pageTitle vTitle=""></v-pageTitle>
    <template>
      <el-table
        :data="tableData4"
        style="width: 100%"
        max-height="650">
        <el-table-column
          fixed
          prop="date"
          label=""
          width="420">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="390">
        </el-table-column>
        <el-table-column
          prop="province"
          label=""
          width="380">
        </el-table-column>

        <el-table-column
          fixed="right"
          label=""
          width="220">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData4)"
              type="text"
              size="small">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle.vue'
  export default {
    name: "dangerPage",
    components:{
      vPageTitle
    },
    data() {
      return {
        tableData4: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-08',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-06',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-07',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      }
    },
  }
</script>

<style scoped>

</style>



