<template>
  <div>
    <v-PageTitle vTitle=""></v-PageTitle>
    <template>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          fixed
          prop="date"
          label=""
          width="350">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="320">
        </el-table-column>
        <el-table-column
          prop="province"
          label=""
          width="320">
        </el-table-column>
        <el-table-column
          prop="city"
          label=""
          width="320">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          width="300">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          width="300">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          width="300">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="100">
          <template slot-scope="scope">
            <el-button type="text" @click="open3"></el-button>
            <el-button  @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle'
  export default {
    name: "tablePage",
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: '455161611@qq.com',
          zip: 200333
        }]
      }
    },
    methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      },
      open3() {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: ': ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
    components:{
      vPageTitle
    }
  }
</script>

<style scoped>

</style>



